.App {
  text-align: center;
}

.dqpl-top-bar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.App-header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  margin: 0;
  padding: 0;
  font-weight: 100;
}

.App-intro {
  font-size: large;
}

.any-colour-you-like {
  text-align: left;
  padding: 2em 1em 0 1em;
  display:-ms-flexbox;
  display:flex;
  /*align-items:flex-center;*/
  -ms-flex-pack:center;
      justify-content:center;
}

.any-colour-you-like button {
  margin: 0;
  margin-left:20px;
}

.any-colour-you-like .dqpl-label.inline {
  margin-right:20px;
}

.any-colour-you-like > .dqpl-field-wrap {
  display:-ms-flexbox;
  display:flex;
  margin-bottom:0;
}

.any-colour-you-like > .dqpl-field-wrap > .dqpl-label {
  margin-top:8px;
}
.inline {
  display: inline-block;
}

.dqpl-field-wrap.inline {
  margin-bottom:0;
}


button.selected-color {
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    color: #666;
    font-size: 15px;
}

.swatch {
    width: 10px;
    height: 10px;
    border: 1px solid #000;
}


